@import "../../../App";
.preference{
  .upload{
    width: 120px;
    background-image: url("../../../assets/upload.svg");
    background-size: 100%;
    background-repeat: no-repeat;
    height: 120px;
  }
  .green-pref{
    color: $main-color;
    font-weight: 500;
  }
  select{
    background-color: transparent;
    padding: 10px 10px 10px 0px;
    border: 0 ;
    border-bottom: 1px solid #F3f3f3;
  }
  button{
    @include buttons($secondary-text, $main-color, $main-color, $width-full)
  }
}