@import "../../../App";

.landing-page-main{
  height: $full-height;
  padding: $padding-side;
  @media (max-width: 600px) {
    padding: $padding-side-mobile;
    height: $full-height-mobile;
  }

  .first{

  }
  .image-cont{
    padding: 20px;
  }
}

.landing-page-search{
  p{
    font-size: map_get($font-sizes, "small");
    font-weight: 500;
  }
  h2{
    font-weight: 600;
  }

  @media (max-width: 600px) {
    padding: $padding-side-mobile;
    align-items: unset;
    justify-content: unset;
  }
}
.landing-image{
  width: 100%;
  max-width: 600px;
}