@import '../../../App';

.looking-job{
  .para-height{
    height: 30px;
  }
  background-image: url("../../../assets/pencil.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
  padding: 10px 10px 30px 10px;
  width: 340px;
  border-radius: 5px;
  margin: 10px 20px;

  @media (max-width: $tablet) {
    width: 100%;
    margin: 10px 0;
    padding: 10px;
  }
  .looking-button{
    margin-top: 15px;
    @media (max-width: $tablet) {
      margin-top: 0px;
    }
  }

}
.looking-candidate{
  .para-height{
    height: 30px;
  }
  background-image: url("../../../assets/bench.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position-y: -35px;
  border-radius: 5px;
  padding: 10px 10px 30px 10px;
  width: 340px;
  margin: 10px 20px;
  @media (max-width: $tablet) {
    width: 100%;
    margin: 10px 0;
  }
}