@import '../../../App';

.job-search-bar{
  .small{
    padding: 1px 0 0 0;
  }
  input{
    padding: 8px;
    border-radius: 2px;
    margin: 1px;
    border: 0 !important;
    background-color: $secondary-text !important;
  }
  select{
    padding: 8px;
    width: 100%;
    border: 0 !important;
    border-radius: 2px;
    background-color: $secondary-text !important;
    &:focus{
      outline-color: transparent !important;
    }
  }
}