@import '../../../App';

.recruiter-card{
  padding: 10px;
  margin: 5px;
  height: auto;
  border-radius: 3px;
  background-color: $secondary-text;
  p{
    color: $gray-text;
    font-weight: 400;
  }
  h6{
    font-weight: 600;
  }
  .details{
    width: 100%;
    height: 60px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}