@import "../../../App";
.register{
  margin-top: -55px;
  min-height: 100vh;
  max-height: 100%;
  .ant-row{
    padding: 10px 0;
    p{
      font-weight: 500;
      margin-bottom: 0 !important;
      color: $gray-text;
    }
  }
  .forms{
    width: 100%;
    max-width: 450px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px 50px 10px;
  }
  .ant-tabs-tab-active{
    color: #023e69 !important;
    &:hover{
      color: #023e69 !important;
    }
  }
  .ant-tabs-tab:hover{
    color: #023e69 !important;
  }
  .ant-tabs-ink-bar{
    background-color: #023e69 !important;
  }

}