@import "../../../App.scss";

.job-application-table{
  th{
    padding-bottom: 20px;
  }
  .ant-tabs-content{
    background-color: red !important;
    box-shadow: 0 0 10px red;
  }
  .applicant-select {
    @include buttons($main-color, $light-green, $light-green);
    margin: 5px;
  }
  .applicant-reject{
    @include buttons($red, $light-red, $light-red);
    margin : 5px;
  }

}
