@import '../../../App';

.landing-page-form{
  .search-button{
    height: 42px;
    @include buttons($secondary-text,#023e69, #023e69, $width-100)
  }
  input{
    background-color: transparent;
    border: 1px solid $gray-text;
    padding: 10px;
    border-radius: 3px;
    padding-left: 30px !important;
    @media (max-width: 700px) {
      padding-left: 8px !important;
    }
  }

}