@import "../../../App";

.company-title{
  padding: 5px;
  height: auto;
  margin: 5px;
  background-color: $secondary-text;
  span{
    color: $gray-text;
  }
  p{
    font-weight: 600;
  }
  h6{
    padding: 10px 0;
    border-bottom: 1px solid #DADADA;
  }
  .small-blue{
    color: #49CEFF !important;
    font-size: map_get($font-sizes, "small") !important;
    border-top: 1px solid #DADADA !important;
    padding: 10px 0 0 0;
    margin-bottom: 3px;
    font-weight: 500 !important;
  }
}