@import '../../../App';

.about{
  padding: $padding-side;
  height: $full-height;

  @media (max-width: 767px) {
    padding: $padding-side-mobile;
    height: auto;
    h1{
      font-size: 150%;
      font-weight: 600;
    }
  }
}
.about-text{
  h1{
    width: 65% !important;
  }
  p{
    width: 80% !important;
    color: $main-text;
  }
  @media (max-width: 600px) {
    h1{
      width: 100% !important;
    }
    p{
      width: 100% !important;
      font-size: 120%;
    }
  }
}