@import '../../../App.scss';

.recruiter-card{
  box-shadow: 0 0 5px #d9d9d9;
  .card-text{
    display: flex;
    align-items: center;
    span{
      color: $main-text;
      font-weight: 500;
      font-size: 18px;
    }
  }


  .recruiter-card-circle{
    width: 100%;
    max-width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100px;
  }
  .green{
    background-color: #E6FFF1;
    color: $main-color;
  }
  .blue{
    background-color: #D4EFFF;
    color: $blue;
  }
  .purple{
    background-color: #F4DFFF;
    color: $purple;
  }

}