@import '../../../App';

.about-card{
  height: 240px;
  padding: 10px 15px;
  width: 100%;
  max-width: 200px;
  border-radius: 3px;
  box-shadow: 0 0 20px #D3D3D3;
  color: $secondary-text;
  background-color: red;
  .about-title{
    font-weight: 700;
  }


  .about-details{
    font-size: map_get($font-sizes, "small");
  }
  @media(max-width: 600px){
    margin: 10px 0;
  }

}
.purple{
  background-color: $orange;
  margin: 0 5px 5px 0 ;
}
.orange{
  background-color: $purple;
  margin: 0 0 5px 5px;
}
.green{
  background-color: $blue;
  margin: 5px 5px 0 0 ;
}
.blue{
  background-color: $main-color;
  margin: 5px 0 0 5px;
}
