@import "../../../App";

.explore{
  text-align: center;
  position: absolute;
  background-color: #ab84a8;
  width: 100%;
  bottom: 0;
  left: 0px;
  padding: 20px 5px;
  @media (max-width: $tablet) {
    padding: 5px 5px;
  }
  h5{
    margin-top: 40px;
  }
  div{
    margin-top: 30px;
    padding-bottom: 30px;
  }
  @media(max-width: 700px){
    h5{
      margin-top: 10px;
    }
    div{
      margin-top: 10px;
      padding-bottom: 10px;
    }
  }
}