@import '../../../App.scss';

.chatbox{
  padding: 10px;
  .chat-footer{
    background-color: $secondary-text;
    border-radius: 0 0 5px 5px;
    border-top: 1px solid #d9d9d9;
    padding: 5px 10px;
    img{
      height: 20px;
    }
    textarea{
      border: 0;
      resize: none;
    }
    textarea:focus{
      outline: 0 !important;
    }
    .chat-sent-button{
      background-color: $main-color;
      padding: 7px;
      border-radius: 50%;
    }
  }
  p{
    margin-bottom: 0!important;
  }
  .chatbox-title{
    background-color: $chat-green;
    border-bottom: 1px solid #d9d9d9;
    border-radius: 5px 5px 0 0;
    padding: 5px 10px;
  }
  .chatbox-container{
    grid-area: chat-message-list;
    overflow-x: hidden;
    .scroll-to-bottom{
      height: 100%;
      width: 100%;
    }
    span{
      color: $gray-text;
      font-size: 70%;
    }
    background-color: $secondary-text;
    height: $chat-box-height;
    overflow-y: scroll;
    .chat-message-other{
      background-color: $chat-green;
      max-width: 60%;
      width: fit-content;
      //min-width: fit-content;
      padding: 10px;
      margin: 5px 10px;
      border-radius: 0 10px 10px 10px;
    }
    .chat-message-self{
      max-width: 60%;
      min-width: fit-content;
      background-color: $chat-gray;
      padding: 10px;
      margin: 5px 10px;
      border-radius: 10px 10px 0 10px;
    }
  }


  .css-y1c0xs::-webkit-scrollbar{
    width: 6px;
  }
  .css-y1c0xs::-webkit-scrollbar-thumb{
    background-color: $chat-green;
    border-radius: 10px;
  }
  .chatbox-container::-webkit-scrollbar{
    width: 0px;
  }
  .chatbox-container::-webkit-scrollbar-thumb{
    background-color: $chat-green;
    border-radius: 10px;
  }

}