@import '../../../App';

.login{
  margin: -55px;
  height: 100vh !important;

  .green{
    color: #023e69;
    background-color: transparent;
  }
  h1{
    margin-bottom: 10px !important;
    @media (max-width: 767px) {
      font-size: 30px !important;
    }

  }
  p{
    color: #023e69;
    font-weight: 500;
  }

  .form{
    max-width: 370px;
    width: 100%;
    padding: 0 5px;
    .field{
      width: 100%;
      padding: 6px;
      border: 1px solid #A7A7A7;
      border-radius: 4px;
      input{
        background-color: transparent !important;
        border: 0 !important;
        width: 100%;
        &:focus{
          outline-color: transparent;
        }
        &::placeholder{
          color: $gray-text;
        }
      }
    }
  }
}