//Colors

$main-color: #29E781;
$purple: #BF5CF7;
$orange: #F8CF61;
$green: #3FF895;
$blue: #49CEFF;
$transparent : transparent;
$very-light : #f8f8f8;
$light-green : #E1FFF1;
$light-red : #FFE8EC;
$red : #FF1037;
$chat-green : #B9FFDA;
$chat-gray : #EEEEEE;


//Text colors
$main-text: #000000;
$gray-text: #A7A7A7;
$secondary-text: #ffffff;
$green-text : #29E781;


//Font size
$font-sizes: (
              "small": 13px,
              "normal": 18px,
              "large" : 26px
              );
//Width
$width-100 : 100px;
$width-150 : 150px;
$width-full : 100%;
$adjust : -55px;
$tablet : 767px;
$chat-box-height : 75vh;

//Box shadow
$box-shadow-explore : 0 0 5px $gray-text;

//Padding
$padding-side : 5px 80px;
$padding-side-mobile : 5px 10px;

//Height
$full-height : calc(100vh - 55px);
$full-height-mobile : calc(100vh - 55px);
$full-height-100: 100vh;
$full-height-perc : calc(100% - 55px);

