@import '../../../App';

.job-listing-card{
  padding: 10px;
  margin: 0 5px 5px 5px;
  height: auto;
  border-radius: 3px;
  border-bottom: 1px solid#d9d9d9;
  background-color: $secondary-text;
  p{
    color: $gray-text;
    font-weight: 500;
    margin-bottom: 5px !important;
  }
  .details{
    width: 100%;
    height: 60px;
    overflow: hidden;
    text-overflow: ellipsis;
    @media (max-width: 767px) {
      font-weight: 400;
    }
  }
}