@import "variable.scss";
html{
  font-size: 18px;

}
//Mixin for Buttons
body{
  padding-top: 55px;
  font-family: "Nunito Sans" !important;
}
//to remove the outline on button click
button:focus{
  outline: 0px !important;
}
input:focus{
  outline: 0px !important;
}
input::placeholder{
  color: $main-text;
}
a{
  color: initial !important;
}

.ant-checkbox-checked .ant-checkbox-inner{
  background-color: $main-color !important;
  border: 1px solid $main-color;
}
.ant-checkbox-checked::after{
  border: 1px solid $main-color !important;
}
@mixin buttons($b-color, $b-bg-color, $b-border-color, $width : 100px, $box-shadow: 0 0 0){
  background-color: $b-bg-color;
  color: $b-color;
  border-radius: 3px;
  border: 1px solid $b-border-color;
  padding: 7px;
  width: $width;
  font-weight: 500;
  box-shadow: $box-shadow;
  //redundant
}

